<template>
  <el-row class="statistics" :gutter="12">
    <el-col :span="4" class="item">
      <el-card shadow="never">
        <p class="statistics-title review-title">
          点评情况
          <span @click="setReviewShow = true">
            <svg class="icon feedback-icon" aria-hidden="true">
              <use xlink:href="#iconquanju_fankui" />
            </svg>
          </span>
        </p>
        <p class="task-volume">
          <span class="task-text">点评咨询数量/人/医生：</span
          ><span class="task-num">{{ taskVolume }}</span>
        </p>
        <p class="task-volume">
          <span class="task-text">{{ getMonth(0) }}月已点评任务量：</span
          ><span class="task-num">{{ statistics.cur_month_total }}</span>
        </p>
        <p class="task-volume">
          <span class="task-text">{{ getMonth(-1) }}月点评任务量：</span
          ><span class="task-num">{{ statistics.pre_month_total }}</span>
        </p>
        <p class="task-volume">
          <span class="task-text">上线健康管理师数量：</span
          ><span class="task-num">{{
            statistics.hm_count ? statistics.hm_count.length : 0
          }}</span>
        </p>
      </el-card>
    </el-col>
    <el-col :span="10" class="item">
      <el-card shadow="never">
        <p class="statistics-title">{{ getMonth() }}月点评量统计</p>
        <div
          v-for="(item, index) in statisticsList"
          :key="index"
          class="review-item"
        >
          <p class="finish-num">累计完成点评数量{{ item.review_count }}</p>
          <p class="task-volume">
            <span class="segment">{{ item.name }}</span>
            <el-progress
              :color="colors[index]"
              :show-text="false"
              :percentage="getNum(item.task_count, item.review_count)"
              :stroke-width="15"
            />
            <span class="segment">{{
              item.task_count === 0 || item.task_count === item.review_count
                ? '已完成'
                : `未完成${getNoFinishNum(item.task_count, item.review_count)}`
            }}</span>
          </p>
        </div>
      </el-card>
    </el-col>
    <el-col :span="10" class="item">
      <el-card shadow="never" class="time-statistics">
        <p class="statistics-title">{{ getMonth() }}月点评用时统计</p>
        <div
          v-for="(item, index) in statisticsList"
          :key="index"
          class="review-item"
        >
          <p class="finish-num">{{ getTime(item.time) }}</p>
          <p class="task-volume">
            <span class="segment">{{ item.name }}</span>
            <el-progress
              :color="colors[index]"
              :show-text="false"
              :percentage="getUseTime(item.time) || 0"
              :stroke-width="15"
            />
          </p>
        </div>
      </el-card>
    </el-col>
    <tip
      :tip-show.sync="setReviewShow"
      btn-text="确定"
      class="center"
      :show-cancel-btn="true"
      :has-footer="true"
      :close-hid="true"
      width="400px"
      title="点评情况设置"
      @btnHandle="btnSetReviewShow"
      @cancel="
        setReviewShow = false
        setReviewTip = ''
      "
    >
      <p slot="content">
        <span>点评咨询数量/人/医生</span>
        <el-input
          v-model="personNum"
          type="number"
          placeholder="请输入数量"
          style="display: inline-block;margin-left: 10px;width: auto;"
          @blur="watchPerson"
        />
        <span class="error">{{ setReviewTip }}</span>
      </p>
    </tip>
  </el-row>
</template>

<script>
import Tip from '@/components/Dialog/index'
import {
  createTaskVolume,
  getTaskVolume,
  getStatisticsContent
} from '@/api/learningAdmin'
export default {
  name: 'ReviewStatistics',
  components: {
    Tip
  },
  data() {
    return {
      colors: [
        [
          { color: '#0085FF', percentage: 0 },
          { color: '#4EABFF', percentage: 100 }
        ],
        [
          { color: '#67CA39', percentage: 0 },
          { color: '#53DE42', percentage: 100 }
        ],
        [
          { color: '#FFB718', percentage: 0 },
          { color: '#FFC74A', percentage: 100 }
        ]
      ],
      setReviewShow: false,
      setReviewTip: '',
      personNum: '',
      taskVolume: '',
      statisticsList: [],
      statistics: {}
    }
  },
  computed: {
    getMonth() {
      return function(cur = 0) {
        return new Date().getMonth() + 1 + cur || 12
      }
    },
    getTime() {
      return function(time) {
        if (time < 1) return `0-1分钟`
        else {
          if (time < 60) return Math.round(time) + '分钟'
          else {
            const hour = Math.round(time / 60)
            const minute = parseInt(time - hour * 60)
            return `${hour}时${minute}分`
          }
        }
      }
    },
    getUseTime() {
      return function(time) {
        if (this.statisticsList.length) {
          const timeTotal = this.statisticsList.reduce((prev, next) => {
            return prev + next.time
          }, 0)
          return Math.ceil((time / timeTotal) * 100)
        } else return 0
      }
    },
    getNum() {
      return function(total, count) {
        if (!total) return 0
        else {
          return Math.ceil((count / total) * 100) > 100
            ? 100
            : Math.ceil((count / total) * 100)
        }
      }
    },
    getNoFinishNum() {
      return function(total, count) {
        if (total - count < 0) return 0
        else return total - count
      }
    }
  },
  created() {
    this.getTaskVolume()
    this.getStatisticsContent()
  },
  methods: {
    async btnSetReviewShow() {
      if (!(this.personNum + '').length) {
        this.setReviewTip = '您输入的内容有误，请重新输入'
        return
      } else this.setReviewTip = ''
      const { code, data } = await createTaskVolume({
        count: parseInt(this.personNum)
      })
      if (code === 20000) {
        if (data.result) {
          this.$message({ message: '修改成功', type: 'success' })
          this.getTaskVolume()
          this.setReviewShow = false
        } else this.$message({ message: data.message, type: 'error' })
      }
    },
    watchPerson() {
      if (!(this.personNum + '').length) {
        this.setReviewTip = '您输入的内容有误，请重新输入'
      } else this.setReviewTip = ''
    },
    async getTaskVolume() {
      const { code, data } = await getTaskVolume()
      if (code === 20000) {
        if (data.result) this.taskVolume = data.task_count
      }
    },
    async getStatisticsContent() {
      const { code, data } = await getStatisticsContent()
      if (code === 20000 && data.result) {
        this.statistics = data.statistics
        const info = data.statistics.dataMap
        for (const i in info) {
          this.statisticsList.push({
            name: info[i].name,
            review_count: info[i].review_count,
            task_count: info[i].task_count,
            time: info[i].time
          })
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
.statistics {
  .item {
    margin: 20px 0;
    height: 237px;
    /deep/.el-card {
      overflow: auto;
      height: 237px;
    }
    .statistics-title {
      font-size: 16px;
      font-weight: 600;
      color: #17233d;
      &:not(.review-title) {
        margin-bottom: 20px;
      }
      &.review-title {
        margin-bottom: 38px;
      }
      .statistics-time {
        color: #0085ff;
      }
      .feedback-icon {
        margin-left: 8px;
        width: 19px;
        height: 19px;
        color: #0085ff;
        cursor: pointer;
        vertical-align: text-bottom;
      }
    }
    .review-item {
      margin-bottom: 22px;
    }
    .task-volume {
      display: flex;
      font-size: 14px;
      color: #485465;
      &:not(:last-child) {
        margin-bottom: 22px;
      }
      .segment {
        overflow: hidden;
        width: 10%;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .el-progress {
        width: 80%;
      }
      .task-num {
        margin-left: 15%;
      }
      .task-text {
        overflow: hidden;
        width: 155px;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .finish-num {
      margin-left: 12%;
      font-size: 12px;
      color: #576277;
      margin-bottom: 6px;
    }
    .time-statistics {
      .segment {
        width: 10%;
      }
      .el-progress {
        width: 90%;
      }
    }
  }
  .center {
    text-align: center;
    /deep/h4 {
      font-size: 14px;
      color: #17233d;
    }
    /deep/.el-dialog__body {
      margin: 10px;
    }
    .error {
      display: block;
      margin-top: 5px;
      margin-left: 124px;
      font-size: 12px;
      color: #f8747f;
    }
  }
}
</style>
