<template>
  <el-row class="review-statistics" :gutter="12">
    <el-col :span="4" class="item">
      <el-card shadow="never">
        <p class="statistics-title">点评统计</p>
        <p class="task-volume">
          {{ getMonth() }}月点评任务量：<span class="task-num">{{
            statistics.task_count
          }}</span>
        </p>
        <p class="task-volume">
          {{ getMonth() }}月待点评数量：<span class="task-num">{{
            statistics.review_count
          }}</span>
        </p>
        <p class="task-volume">
          {{ getMonth(-1) }}月点评数量：<span class="task-num">{{
            statistics.pre_month
          }}</span>
        </p>
      </el-card>
    </el-col>
    <el-col :span="6" class="item">
      <el-card shadow="never">
        <p class="statistics-title">
          点评用时：<span class="statistics-time"
            >{{ statistics.time || 0 }}分钟</span
          >
        </p>
        <el-progress
          type="circle"
          :percentage="getCarryOut || 0"
          :width="130"
          :stroke-width="10"
          >已完成</el-progress
        >
      </el-card>
    </el-col>
    <el-col :span="10" class="item">
      <el-card shadow="never">
        <p class="statistics-title">{{ getMonth() }}月点评分数统计</p>
        <div
          v-for="(item, index) in scoreStatistics"
          :key="index"
          class="review-item"
        >
          <p class="task-volume">
            <span class="segment">{{ item.title }}</span>
            <el-progress
              :color="colors[index]"
              :show-text="false"
              :percentage="getScoreTotal(item.count)"
              :stroke-width="15"
            />
            <span class="segment pl">{{ item.count }}个</span>
          </p>
        </div>
      </el-card>
    </el-col>
    <el-col :span="4" class="item">
      <el-card shadow="never" class="doctor-comment">
        <el-button type="primary" @click="goToReview">去点评</el-button>
      </el-card>
    </el-col>
    <el-dialog
      title="提示"
      :visible.sync="hasReview"
      width="400px"
      center
      top="30vh"
    >
      <p style="text-align: center;">当前没有可点评的咨询记录，请稍后重试</p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="hasReview = false">取 消</el-button>
        <el-button type="primary" @click="hasReview = false">确 定</el-button>
      </span>
    </el-dialog>
  </el-row>
</template>

<script>
import { getStatisticsContent, goToReview } from '@/api/learningAdmin'
export default {
  name: 'ReviewStatistics',
  data() {
    return {
      colors: [
        [
          { color: '#0085FF', percentage: 0 },
          { color: '#4EABFF', percentage: 100 }
        ],
        [
          { color: '#4160FF', percentage: 0 },
          { color: '#95A5FF', percentage: 100 }
        ],
        [
          { color: '#67CA39', percentage: 0 },
          { color: '#53DE42', percentage: 100 }
        ],
        [
          { color: '#FFB718', percentage: 0 },
          { color: '#FFC74A', percentage: 100 }
        ],
        [
          { color: '#F8747F', percentage: 0 },
          { color: '#F8A1AB', percentage: 100 }
        ]
      ],
      hasReview: false,
      statistics: {},
      scoreStatistics: [],
      scoreTotal: 0
    }
  },
  computed: {
    getMonth() {
      return function(cur = 0) {
        return new Date().getMonth() + 1 + cur || 12
      }
    },
    getCarryOut() {
      if (!this.statistics.task_count) return 0
      const carry = Math.ceil(
        (this.statistics.cur_month_total / this.statistics.task_count) * 100
      )
      if (!carry) return 0
      if (carry > 100) return 100
      else return carry
    },
    getScoreTotal() {
      return function(count) {
        if (this.scoreTotal === 0) return 0
        else return Math.ceil((count / this.scoreTotal) * 100)
      }
    }
  },
  created() {
    this.getStatisticsContent()
  },
  methods: {
    async goToReview() {
      const { code, data } = await goToReview()
      if (code === 20000) {
        if (data.result && data.messages && data.messages.fd_ask) {
          this.$router.push('/learning-admin/comment/commit-review')
        } else this.hasReview = true
      }
    },
    async getStatisticsContent() {
      const { code, data } = await getStatisticsContent()
      if (code === 20000 && data.result) {
        this.statistics = data.statistics
        for (const i in data.statistics.dataMap) {
          this.scoreStatistics.push({
            title: i,
            count: data.statistics.dataMap[i]
          })
          this.scoreTotal += data.statistics.dataMap[i]
        }
      }
    }
  }
}
</script>

<style scoped lang="scss">
.review-statistics {
  .item {
    margin: 20px 0;
    height: 256px;
    /deep/.el-card {
      height: 256px;
    }
    .statistics-title {
      font-size: 16px;
      font-weight: 600;
      color: #17233d;
      margin-bottom: 24px;
      .statistics-time {
        color: #0085ff;
      }
    }
    .review-item {
      margin-bottom: 22px;
    }
    .task-volume {
      display: flex;
      font-size: 14px;
      color: #485465;
      &:not(:last-child) {
        margin-bottom: 22px;
      }
      .segment {
        width: 12%;
        &.pl {
          padding-left: 10px;
        }
      }
      .el-progress {
        width: 76%;
      }
      .task-num {
        margin-left: 15%;
      }
    }
  }
  .el-progress--circle {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 180px;
    text-align: center;
  }
  .doctor-comment {
    /deep/.el-card__body {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
    }
  }
  /deep/.el-button {
    padding: 6px 20px;
    height: 32px;
  }
}
</style>
