<template>
  <div class="consulting-review">
    <el-form
      label-position="left"
      label-width="100px"
      :model="consulting"
      inline
    >
      <el-form-item label="被点评人" class="search-item">
        <el-select
          v-model="consulting.reviewer"
          clearable
          filterable
          placeholder="请选择"
        >
          <el-option
            v-for="item in reviewerList"
            :key="item.value"
            :label="item.name"
            :value="item._id"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="分数" class="search-item">
        <el-select v-model="consulting.score" clearable placeholder="请选择">
          <el-option
            v-for="item in scoreList"
            :key="item.title"
            :label="item.title"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="点评时间" clearable class="search-item">
        <el-date-picker
          v-model="reviewTime"
          :type="type === 'human_resource' ? 'monthrange' : 'daterange'"
          align="right"
          unlink-panels
          range-separator="-"
          start-placeholder="点评时间"
          end-placeholder="点评时间"
          value-format="yyyy-MM-dd"
        />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="select-btn" @click="select"
          >查询</el-button
        >
      </el-form-item>
    </el-form>
    <el-divider v-if="character !== 'hm'" />
    <div class="list">
      <div class="title">
        <div>点评列表</div>
      </div>
      <el-table :data="list" style="width: 100%" :empty-text="hasListInfo">
        <el-table-column prop="reviewTime" label="点评时间" align="center" />
        <el-table-column prop="reviewer" label="被点评人" align="center" />
        <el-table-column
          v-if="type === 'human_resource'"
          prop="commentatorNum"
          label="已评人数"
          align="center"
        />
        <el-table-column
          v-if="type === 'human_resource'"
          prop="commentNum"
          label="评价条数"
          align="center"
        />
        <el-table-column
          prop="score"
          :label="type === 'human_resource' ? '最终分数' : '分数'"
          align="center"
        />
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="examine(scope.row)"
              >查看</el-button
            >
            <span v-if="scope.row.un_read" class="tip-spot" />
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          :current-page="currentPage"
          :page-sizes="[12, 18, 24]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
import {
  getReviewList,
  getMonthList,
  getBusUser,
  setReadStatus
} from '@/api/learningAdmin'
export default {
  name: 'ConsultingReview',
  data() {
    return {
      consulting: {
        reviewer: '',
        score: [0, 100]
      },
      reviewerList: [],
      scoreList: [
        { title: '全部', value: [0, 100] },
        { title: '91-100', value: [91, 100] },
        { title: '81-90', value: [81, 90] },
        { title: '71-80', value: [71, 80] },
        { title: '61-70', value: [61, 70] },
        { title: '60以下', value: [0, 60] }
      ],
      reviewTime: ['', ''],
      hasListInfo: '暂无数据',
      list: [],
      currentPage: 1,
      pageSize: 12,
      count: 0,
      type: this.$store.state.user.mts_user
        ? this.$store.state.user.mts_user.type
        : '',
      character: this.$store.state.user.bus_role // 用户角色
    }
  },
  created() {
    this.getReviewList()
    this.getBusUser()
  },
  methods: {
    async select() {
      this.currentPage = 1
      await this.getReviewList(this.consulting)
      if (!this.list.length) this.hasListInfo = '暂无数据，请重新输入关键词'
      else this.hasListInfo = '暂无数据'
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getReviewList(this.consulting)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getReviewList(this.consulting)
    },
    examine(row) {
      setReadStatus({
        id: row.id
      })
      if (this.type !== 'human_resource') {
        this.$router.push('/learning-admin/comment/see-reviews/' + row.id)
      } else {
        this.$router.push(
          '/learning-admin/comment/review-detail/' +
            row.id +
            '?date=' +
            row.reviewTime
        )
      }
    },
    async getReviewList(search) {
      const params = {
        page: this.currentPage,
        pageSize: this.pageSize
      }
      if (search) {
        if (search.reviewer) params.commentator = search.reviewer
        if (search.score) params.start_score = search.score[0]
        if (search.score) params.end_score = search.score[1]
        if (this.reviewTime && this.reviewTime[0]) {
          params.review_started_at = this.reviewTime[0]
        }
        if (this.reviewTime && this.reviewTime[1]) {
          params.review_ended_at = this.reviewTime[1]
        }
      }
      if (this.type !== 'human_resource') {
        const { data, code } = await getReviewList(params)
        if (code === 20000) {
          this.count = data.count
          if (data.list.length) {
            this.list = data.list.map(item => {
              return {
                reviewTime: item.created_at.split('T')[0],
                reviewer: item.commentator ? item.commentator.name : '/',
                score: item.score,
                id: item._id,
                un_read: item.un_read
              }
            })
          } else this.list = data.list
        }
      } else {
        const { code, data } = await getMonthList(params)
        if (code === 20000) {
          this.count = data.count
          if (data.list.length) {
            this.list = data.list.map(item => {
              return {
                reviewTime:
                  item._id.year +
                  '-' +
                  (item._id.month < 10 ? '0' + item._id.month : item._id.month),
                reviewer: item._id.name || '/',
                commentatorNum: item.reviewer_count,
                commentNum: item.count,
                score: Math.round(item.score),
                id: item._id.commentator,
                un_read: item.un_read
              }
            })
          } else this.list = data.list
        }
      }
    },
    async getBusUser() {
      const { code, data } = await getBusUser({
        bus_role: 'hm'
      })
      if (code === 20000) {
        this.reviewerList = data.list
      }
    }
  }
}
</script>

<style scoped lang="scss">
.consulting-review {
  .search-item {
    margin-right: 70px;
    color: #17233d;
    /deep/input {
      width: 220px;
      height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .pagination {
    margin-top: 50px;
    text-align: center;
  }
  .list {
    margin: 8px 0;
    padding: 20px;
    width: 100%;
    border: 1px solid #ebeef5;
    .title {
      padding-bottom: 30px;
      font-size: 20px;
      font-weight: bold;
      color: #333;
    }
  }
  /deep/.el-divider--horizontal {
    margin: 8px 0 30px;
  }
  .tip-spot {
    position: relative;
    top: -5px;
    display: inline-block;
    width: 3px !important;
    height: 3px;
    border: 2px solid #f00;
    border-radius: 50%;
  }
}
</style>
