<template>
  <div class="topic-list">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="训练点评" name="first">
        <!--  点评列表  -->
        <el-form
          label-position="left"
          label-width="100px"
          :model="topic"
          inline
        >
          <el-form-item label="题型" class="search-item">
            <el-select v-model="topic.type" clearable placeholder="请选择">
              <el-option
                v-for="item in typeList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <template v-if="isOpen">
            <el-form-item label="答题时间" clearable class="search-item">
              <el-date-picker
                v-model="time"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="-"
                start-placeholder="答题时间"
                end-placeholder="答题时间"
                value-format="yyyy-MM-dd"
              />
            </el-form-item>
            <el-form-item label="试题难度" class="search-item">
              <el-select
                v-model="topic.difficult"
                clearable
                placeholder="请选择"
              >
                <el-option
                  v-for="item in difficultyList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </el-form-item>
            <el-form-item label="学员姓名" class="search-item">
              <el-input
                v-model="topic.name"
                clearable
                placeholder="请输入学员姓名"
              />
            </el-form-item>
          </template>
          <el-form-item label="单节课名称" class="search-item">
            <el-select v-model="topic.catalog" clearable placeholder="请选择">
              <el-option
                v-for="item in singleList"
                :key="item._id"
                :label="item.title"
                :value="item._id"
              />
            </el-select>
          </el-form-item>
          <template v-if="isOpen">
            <el-form-item label="所属阶段" class="search-item">
              <el-select v-model="topic.stage" clearable placeholder="请选择">
                <el-option
                  v-for="item in stageList"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                />
              </el-select>
            </el-form-item>
          </template>
          <el-form-item>
            <el-button type="primary" class="select-btn" @click="onSubmit"
              >查询</el-button
            >
          </el-form-item>
          <el-button type="text" class="arrow" @click="isOpen = !isOpen">
            {{ isOpen ? '收起' : '展开' }}
            <i
              :class="isOpen ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
              style="font-size: 16px;color: #0085FF"
            />
          </el-button>
        </el-form>
        <!--  列表  -->
        <div class="list">
          <div class="title">
            <div>点评列表</div>
          </div>
          <el-table :data="list" style="width: 100%" :empty-text="hasListInfo">
            <el-table-column
              fixed
              prop="num"
              label="编号"
              min-width="100"
              align="center"
            />
            <el-table-column
              prop="stem"
              label="试题内容"
              min-width="120"
              align="center"
            >
              <template slot-scope="scope">
                <el-popover
                  placement="top-start"
                  trigger="hover"
                  :content="scope.row.stem"
                >
                  <span slot="reference" class="cell" v-html="scope.row.stem" />
                </el-popover>
              </template>
            </el-table-column>
            <el-table-column
              prop="title"
              label="单节课名称"
              min-width="120"
              align="center"
            />
            <el-table-column
              prop="stage"
              label="所属阶段"
              min-width="100"
              align="center"
            />
            <el-table-column
              prop="type"
              label="题型"
              min-width="80"
              align="center"
            />
            <el-table-column
              prop="difficult"
              label="难度"
              min-width="80"
              align="center"
            />
            <el-table-column
              prop="name"
              label="学员姓名"
              min-width="80"
              align="center"
            />
            <el-table-column
              prop="updated_at"
              label="答题时间"
              min-width="120"
              align="center"
            />
            <el-table-column
              prop="status"
              label="答题结果"
              min-width="80"
              align="center"
            />
            <el-table-column
              fixed="right"
              label="操作"
              width="150"
              align="center"
            >
              <template slot-scope="scope">
                <el-button
                  type="text"
                  size="small"
                  :disabled="scope.row.type !== '简单题'"
                  @click="goComment(scope.row)"
                  >去点评</el-button
                >
              </template>
            </el-table-column>
          </el-table>
          <div class="pagination">
            <el-pagination
              :current-page="currentPage"
              :page-sizes="[12, 18, 24]"
              :page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="count"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
            />
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="咨询点评" name="second">
        <hr-statistics v-if="type === 'human_resource'" />
        <review-statistics v-if="character === 'doctor'" />
        <consulting-review :class="character !== 'hm' ? 'review-list' : ''" />
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { getAllSingle, reviewList } from '@/api/learningAdmin'
import ConsultingReview from './components/ConsultingReview'
import ReviewStatistics from './components/ReviewStatistics'
import HrStatistics from './components/HrStatistics'
export default {
  components: {
    ConsultingReview,
    ReviewStatistics,
    HrStatistics
  },
  data() {
    return {
      isOpen: false,
      list: [],
      currentPage: 1,
      delShow: false,
      pageSize: 12,
      count: 0,
      rowInfo: '',
      hasListInfo: '暂无数据',
      singleList: [],
      createNameList: [],
      time: ['', ''],
      activeName: 'first',
      type: this.$store.state.user.mts_user
        ? this.$store.state.user.mts_user.type
        : '',
      character: this.$store.state.user.bus_role,
      topic: {
        type: '',
        name: '',
        stage: '',
        status: '',
        difficult: '',
        catalog: '',
        started_at: '',
        ended_at: ''
      },
      typeList: [
        { value: 'all', label: '全部' },
        { value: 'radio', label: '单选题' },
        { value: 'checkbox', label: '多选题' },
        { value: 'short_answer', label: '简答题' }
      ],
      stageList: [
        { value: 'all', label: '全部' },
        { value: '0', label: '第一阶段' },
        { value: '1', label: '第二阶段' },
        { value: '2', label: '第三阶段' },
        { value: '3', label: '第四阶段' },
        { value: '4', label: '第五阶段' },
        { value: '5', label: '第六阶段' }
      ],
      difficultyList: [
        { value: 'all', label: '全部' },
        { value: '1', label: '简单' },
        { value: '2', label: '正常' },
        { value: '3', label: '难' }
      ]
    }
  },
  watch: {
    time(v) {
      this.topic.started_at = v[0]
      this.topic.ended_at = v[1]
    }
  },
  created() {
    const query = this.$route.query
    this.time = [query.started_at || '', query.ended_at || '']
    if (query.page) this.currentPage = Number(query.page)
    if (query.pageSize) this.pageSize = Number(query.pageSize)
    this.isExistNegativeValue(this.topic, query)
    this.getQuestionList(query)
    this.getAllSingle()
  },
  methods: {
    isExistNegativeValue(n, o) {
      n = n || {}
      o = o || {}
      for (const key in n) {
        if (n.hasOwnProperty(key) && o.hasOwnProperty(key)) {
          n[key] = o[key]
        }
      }
    },
    getReviewType(type) {
      switch (type) {
        case 'radio':
          return '单选题'
        case 'checkbox':
          return '多选题'
        case 'short_answer':
          return '简单题'
        default:
          return ''
      }
    },
    getStageText(type) {
      switch (type) {
        case -1:
          return '试岗阶段'
        case 0:
          return '第一阶段'
        case 1:
          return '第二阶段'
        case 2:
          return '第三阶段'
        case 3:
          return '第四阶段'
        case 4:
          return '第五阶段'
        case 5:
          return '第六阶段'
        default:
          return ''
      }
    },
    getDifficultText(type) {
      switch (type) {
        case '1':
          return '简单'
        case '2':
          return '正常'
        case '3':
          return '困难'
      }
    },
    getStatusText(status) {
      switch (status) {
        case 'error':
          return '错误'
        case 'success':
          return '正确'
        default:
          return '/'
      }
    },
    onSubmit() {
      this.currentPage = 1
      this.getQuestionList(
        this.isEmpty({ ...this.topic, page: 1, pageSize: this.pageSize })
      )
    },
    isEmpty(obj) {
      const data = {}
      obj = obj || {}
      for (const key in obj) {
        if (
          Object().toString.call(obj[key]) === '[object Null]' ||
          (Object().toString.call(obj[key]) === '[object String]' &&
            !obj[key].trim())
        ) {
          continue
        }
        if (obj.hasOwnProperty(key)) {
          data[key] = obj[key]
        }
      }
      return data
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getQuestionList(
        this.isEmpty({ ...this.topic, page: 1, pageSize: val })
      )
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getQuestionList(
        this.isEmpty({ ...this.topic, page: val, pageSize: this.pageSize })
      )
    },
    async getQuestionList(query) {
      // 获取试题列表
      if (query) {
        this.hasListInfo = '暂无数据，请重新输入关键词'
      } else {
        this.hasListInfo = '暂无数据'
      }
      const { data, code } = await reviewList(query)
      if (code === 20000) {
        this.count = data.count
        this.list = data.list.map((item, index) => {
          return {
            num: (data.page - 1) * data.pageSize + index + 1,
            id: item._id,
            stem: item.stem.replace(/<\/?.+?>/g, ''),
            is_review:
              (!item.diagnosis_review || !item.process_review) &&
              item.type === 'short_answer',
            title: item.catalog.title,
            stage: this.getStageText(item.stage),
            type: this.getReviewType(item.type),
            difficult: this.getDifficultText(item.difficult),
            name: item.name,
            status: this.getStatusText(item.status),
            updated_at: item.updated_at.split('T')[0]
          }
        })
      }
    },
    async getAllSingle() {
      const { code, data } = await getAllSingle()
      if (code === 20000) {
        this.singleList = data.list
      }
    },
    goComment(row) {
      if (!row.is_review) {
        return false
      }
      this.$router.push('/learning-admin/comment/management/' + row.id)
    },
    handleClick(tab, event) {
      this.$route.meta.title = tab.label
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (to.meta.title === '咨询点评') vm.activeName = 'second'
    })
  }
}
</script>

<style lang="scss" scoped type="text/scss">
.topic-list {
  display: block;
  flex-wrap: wrap;
  font-size: 14px;
  margin-bottom: 20px;
  .title {
    padding-bottom: 30px;
    font-size: 20px;
    font-weight: bold;
    color: #333;
  }
  /deep/form {
    width: 100%;
    .el-date-editor {
      padding: 0 10px;
      width: 220px;
      height: 32px;
      .el-range-input {
        width: 85px;
        height: 30px;
      }
    }
  }
  .search-item {
    margin-right: 70px;
    color: #17233d;
    /deep/input {
      width: 220px;
      height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .arrow {
    float: right;
  }
  .list {
    margin: 20px 0;
    padding: 20px;
    width: 100%;
    border: 1px solid #ebeef5;
    .title {
      display: flex;
      justify-content: space-between;
      line-height: 32px;
      /deep/button {
        padding: 6px 20px;
        height: 32px;
      }
    }
    /deep/.el-dialog__wrapper {
      button {
        padding: 6px 20px;
        height: 32px;
      }
      .el-dialog__headerbtn {
        top: 10px;
        right: 5px;
      }
    }
    .pagination {
      margin-top: 50px;
      text-align: center;
    }
  }
  /deep/.cell {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
    *:not(button) {
      display: inline-block;
      width: 100%;
    }
    p {
      overflow: hidden;
      -ms-text-overflow: ellipsis;
      text-overflow: ellipsis;
      white-space: nowrap;
      &:hover {
        overflow: auto;
        white-space: pre-wrap;
      }
    }
  }
  /deep/.el-tabs__nav-scroll {
    padding-left: 20px;
    .el-tabs__item {
      &:not(#tab-first) {
        padding: 0 30px 0 20px;
        height: 50px;
        line-height: 50px;
      }
    }
    .el-tabs__active-bar {
      width: 56px !important;
    }
  }
  .review-list {
    padding: 36px 20px;
    border: 1px solid #ebeef5;
    border-radius: 4px;
    /deep/.list {
      padding: 0;
      border: none;
    }
  }
}
</style>
